 -------------------------------------*/

.switch input {
  position: absolute;
  opacity: 0;
}

.switch {
  display: inline-block;
  font-size: 2rem; /* 1 */
  height: 1em;
  width: 2em;
  background: #bdb9a6;
  border-radius: 1em;
  cursor: pointer;
}

.switch div {
  height: 1em;
  width: 1em;
  border-radius: 1em;
  background: #fff;
  transition: all 300ms;
}

.switch input:checked + div {
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}

.top {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}

.bottom {
  -webkit-transform: translate(-50%, calc(60% + 2rem));
  transform: translate(-50%, calc(60% + 2rem));
}

.right {
  -webkit-transform: translate(0, 4.2rem);
  transform: translate(0, 4.2rem);
}

.left {
  -webkit-transform: translate(0, 4.2rem);
  transform: translate(0, 4.2rem);
}

.bottomBadge {
  -webkit-transform: translate(-50%, -20%);
  transform: translate(-50%, -20%);
}

.rightBadge {
  -webkit-transform: translate(-20%, -50%);
  transform: translate(-20%, -50%);
}

.leftBadge {
  -webkit-transform: translate(20%, -50%);
  transform: translate(20%, -50%);
}

.topBadge {
  -webkit-transform: translate(-50%, 20%);
  transform: translate(-50%, 20%);
}

@media (min-width: 640px) {
  .bottomBadge {
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
  }

  .rightBadge {
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }

  .leftBadge {
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }

  .topBadge {
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
  }
}

@media (min-width: 768px) {
  .top {
    -webkit-transform: translateX(calc(-100% - 2.5rem));
    transform: translateX(calc(-100% - 2.5rem));
  }

  .bottom {
    -webkit-transform: translate(2.5rem, 2rem);
    transform: translate(2.5rem, 2rem);
  }

  .right {
    -webkit-transform: translate(calc(100% + 2rem), -1.5rem);
    transform: translate(calc(100% + 2rem), -1.5rem);
  }

  .left {
    -webkit-transform: translate(calc(-100% - 2rem), -1.5rem);
    transform: translate(calc(-100% - 2rem), -1.5rem);
  }
}

