  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.075);
  background-color: rgb(78, 78, 78);
}

.emoji-mart-anchor-icon svg {
  margin: auto;
}

.fadein {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  transition: all 1s;
  -webkit-animation: ease-in slide 0.5s;
  animation: ease-in slide 0.5s;
}

@-webkit-keyframes slide {
  from {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }

  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes slide {
  from {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }

  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.fadeout {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
  transition: 0.5s;
}

.countdown {
  transition: all 1s;
  -webkit-animation: linear count 10s;
  animation: linear count 10s;
}

@-webkit-keyframes count {
  from {
    width: 100%;
  }

  to {
    width: 0%;
  }
}

@keyframes count {
  from {
    width: 100%;
  }

  to {
    width: 0%;
  }
}

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 6rem;
  height: 6rem;
}

.lds-ellipsis div {
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis div:nth-child(1) {
  left: 8px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
  animation: lds-ellipsis1 0.6s infinite;
}

.lds-ellipsis div:nth-child(2) {
  left: 8px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
  left: 32px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(4) {
  left: 56px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
  animation: lds-ellipsis3 0.6s infinite;
}

@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
}

@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(24px, 0);
    transform: translate(24px, 0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(24px, 0);
    transform: translate(24px, 0);
  }
}

.enter {
  -webkit-animation: slideshowin 0.4s ease-in;
  animation: slideshowin 0.4s ease-in;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}

@-webkit-keyframes slideshowin {
  from {
    opacity: 0%;
  }

  to {
    opacity: 100%;
  }
}

@keyframes slideshowin {
  from {
    opacity: 0%;
  }

  to {
    opacity: 100%;
  }
}

